import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import { Container, Tab, Tabs, Paper } from '@material-ui/core';
import NavBar from '../Navbar';
import Background from '../../Images/cassino-img.jpg';
import Logo from '../../Images/logo-casino.png';
import Input from '@material-ui/icons/Input';

import "./header.css"


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
    display: "flex-row",
  },
  // title: {
  //   flexGrow: 1,
  // },
  hTop: {
    height: 150,
    borderBottomLeftRadius: "500px 25px ",
    borderBottomRightRadius: "500px 25px ",
    backgroundColor: "white",
    color: "black",
    display: "flex",
    justifyContent: "space-between",
    boxShadow: "0px 20px 50px ",
  },
  hBottom: {
    height: 250,
    borderTopLeftRadius: "500px 25px",
    borderTopRightRadius: "500px 25px ",
    backgroundColor: "white",
    color: "black",
    display: "flex",
    justifyContent: "center",
   
  },

  EnterButton: {
    fontSize: "35px",
    padding: "10px 35px",
    borderRadius: "35px",
    alignItems: "center",
    backgroundColor: "red",
    color: "white",
    fontWeight: "bolder",
    boxShadow: "0px 10px 10px 5px black",
  },

  header: {
    backgroundImage: `url(${Background})`,
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    padding: "0px 0px",
    // maxWidth: "100%",
    
  },

  
  textHeader: {
    color: "white",
    textAlign: "center",
    fontFamily: "Arial, Helvetica, sans-serif",
    fontWeight: "bold",
    textShadow: "0px 10px 10px black",
  },
  exit:{
    float:'right'
  }
  
}));



export default function ButtonAppBar() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Container className={classes.header}>
        <AppBar position="static" className={classes.hTop}>
          <Container>
            <Button className={classes.exit} color="inherit"><Input /></Button>
            <IconButton
                  edge="start"
                  color="inherit"
                  aria-label="menu"
                >
                  <MenuIcon />
                </IconButton>
            <Toolbar >
            
              <Container className={classes.menuButton}>
                
                    <img src={Logo} className="img" alt="asd" />
                
              </Container>
            </Toolbar>
            
          </Container>
        </AppBar>
        <Container>
            <NavBar />
        </Container>
        
        <Container className={classes.textHeader}>
          <h4 variant="h4" className="title">Um Imperdível Bônus</h4>
          <h1 variant="h1" className="title">
            R$:5000
          </h1>
          <h4 variant="h4"  className="title">
            BÔNUS POR DEPÓSITO
          </h4>
        </Container>

        <Container  className={classes.hBottom}>
          <div>
            <Button
              size="large"
              variant="contained"
              className={classes.EnterButton}
            >
              REGISTRE-SE
            </Button>
            {/* <img src={Background} alt="logo"  /> */}
            {/* {<input type= "image" className="img" onclick=" " src="../../images/cassino-img.jpg" />} */}
          </div>
        </Container>
      </Container>
    </div>
  );
}
